}


.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 230px;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #99ffff;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      // -webkit-transform: rotateY(180deg);
      //         transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0;
        transform: scale(0.987) translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0%);
    }
}





  


.owl-carousel .owl-item img {
    width: auto;
}







.search-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: #00000090;
  .close {
    opacity: 0.9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #99ffff;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .zx-search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      border-radius: 50px;
      padding: 0 20px;
      box-shadow: 0px 0px 10px 0px #99ffff80;
      &::placeholder {
        font-family: ar-reg;
      }
    }
    button {
      font-size: 18px;
      color: rgb(15, 15, 15);
      position: absolute;
      top: 50%;
      left: 20px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
      [dir="ltr"] & {
        left: unset;
        right: 20px;
      }
      &:hover {
        color: #99ffff;
      }
    }
  }
}








/*------ lang-switcher start ------*/
  .lang-switcher {
    position: absolute;
    top: 14px;
    left: 0;
    [dir="ltr"] & {
      left: unset;
      right: 0;
    }
    font-family: ar-reg;
    font-size: 12px;
    color: #999;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    > img {
      margin: 0 8px;
    }
    > span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: "";
        font-family: "Linearicons-Free";
        font-weight: 600;
        font-size: 8px;
        color: #999;
        padding: 0 10px;
        transition: all .3s ease-in-out;
        // [dir="ltr"] & {
        //   padding: 0 0 0 8px;
        // }
      }
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 50%;
      z-index: 9;
      transform: translateX(-50%);
      min-width: 110px;
      width: max-content;
      background-color: #fff;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
      color: #000;
      border-radius: 5px;
      padding: 8px 18px;
      transition: all .5s ease-in-out;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      a {
        display: block;
        margin: 3px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #99ffff;
        }
      }
      span {
        padding: 0 7px;
      }
    }
    &:hover {
      > span {
        color: #99ffff;
        &::after {
          color: #99ffff;
        }
      }
      .switch {
        top: 23px;
        opacity: 1;
        visibility: visible;
      }
    }
  }
/*------ lang-switcher end ------*/




/*------ navbar start ------*/
.nav-bar {
  position: relative;
  background-color: #fff;
  // box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
  z-index: 9;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 24px 0;
  }
  .container {
    position: relative;
  }
  .logo-container {
    // width: 230px;
    .logo {
      width: 255px;
    }
  }
  .nav-search {
    position: relative;
    width: 260px;
    height: 45px;
    margin: 10px 0 0 0;
    background-color: #f7f7f7;
    border-radius: 50px;
    transition: all .3s ease-in-out;
    input {
      width: 100%;
      height: 100%;
      border: none;
      border-style: solid;
      border-width: 1px;
      border-color: #fff;
      border-radius: 50px;
      outline: none;
      background-color: transparent;
      padding: 0 25px;
      transition: all .3s ease-in-out;
      &::placeholder {
        font-family: ar-reg;
        font-size: 14px;
        color: #bbb;
      }
      &:hover, &:focus {
        border-color: #666633;
      }
    }
    button {
      font-size: 16px;
      color: #727071;
      position: absolute;
      top: 50%;
      left: 20px;
      transform: translateY(-50%);
      transition: all .3s ease-in-out;
      width: 16px;
      height: 16px;
      // width: 38px;
      // height: 38px;
      // border-radius: 50%;
      // background-color: #bd1522;
      // border: 1px solid #bd1522;
      &:hover {
        // background-color: #f5f5f5;
        color: #666633;
      }
      [dir="ltr"] & {
        left: unset;
        right: 20px;
      }
    }
  }
  .icons {
    margin: 10px 0 0 0;
    width: 255px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    .icon-container {
      position: relative;
      font-size: 18px;
      color: #7a6144;
      margin: 0 0 0 20px;
      [dir="ltr"] & {
        margin: 0 20px 0 0;
      }  
      &:last-child {
        margin: 0;
      }
      a {
        display: flex;
        justify-content: center;
        align-items: center;
      }
      i {
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
      span {
        font-family: ar-reg;
        font-size: 14px;
        color: #7a6144;
        transition: all .3s ease-in-out;
      }
      &:hover {
        color: #99ffff;
        span {
          color: #99ffff;
        }
      }
    }
    .menu-icon, .search-icon {
      display: none;
    }
    .cart-icon {
      .counter {
        position: absolute;
        top: -5px;
        right: -4px;
        width: 15px;
        height: 15px;
        padding: 1px 0 0 0;
        color: #ffffff;
        border-radius: 50%;
        background-color: #9cebe6;
        font-family: ar-reg;
        font-size: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
        [dir="ltr"] & {
          right: unset;
          left: -4px;
        }
      }
      &:hover {
        .counter {
          transform: rotateY(360deg);
          background-color: #7a6144;
          color: #9cebe6;
        }
      }
    }
  }
  
}


/*------ navbar end ------*/



/*------ navlist start ------*/
.nav-list {
  width: 100%;
  background-color: #fff;
  .zx-container {
    padding: 15px 0;
    background-color: #fff;
    border-top: 1px solid #e6e6e6;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .list {
    display: flex;
    justify-content: center;
    align-items: center;
    li {
      position: relative;
      font-family: ar-md;
      font-size: 17px;
      color: #666633;
      text-transform: uppercase;
      margin: 0 15px;
      transition: all .3s ease-in-out;
      // [dir="ltr"] & {
      //   margin: 0 15px 0 0;
      // }
      // &:last-child {
      //   margin: 0;
      // }
      [dir="ltr"] & {
        font-size: 16px;
        margin: 0 13px;
      }
      &:hover {
        color: #9cebe6;
        .sub-cat-hover {
          opacity: 1;
          visibility: visible;
          top: 30px;
        }
      }
    }
  }

  .sub-cat {
    .sub-cat-hover {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      right: -25px;
      [dir="ltr"] & {
        right: unset;
        left: -25px;
      }
      // transform: translateX(-50%);
      min-width: 190px;
      min-height: 200px;
      background-color: rgba(122, 97, 68, 0.949);
      box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
      border-radius: 10px;
      padding: 7px 13px;
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      z-index: 9;
      transition: all .5s ease-in-out;
      li {
        width: 100%;
        transition: all .3s ease-in-out;
        font-family: ar-reg;
        font-size: 15px;
        color: #fff;
        border-bottom: 1px solid #ffffff80;
        padding: 7px 9px;
        margin: 0;
        &:last-child {
          border: none;
        }
        &:hover {
          color: #9cebe6;
        }
      }
    }
  }
}
/*------ navlist end ------*/





/*------ header start ------*/
.header {
  // margin: 20px 0 0 0;
    .owl-carousel {
      position: relative;
